import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router'
import {GrobotDataService} from '../../_shared/grobot-data.service'
import {SocketService} from '../../_shared/socket.service'
@Component({
  selector: 'grobot-timer',
  templateUrl: './timer.component.html',
  styleUrls: ['./timer.component.css']
})
export class TimerComponent implements OnInit {


  timer1:any;
  timer2:any;
  timer3:any;
  timer4:any;

  selected:any = [true,false,false,false];
  selectedCh:number = 0;

  show:boolean = true;
  constructor(private data:GrobotDataService,private route:Router,private io:SocketService) {  }

  ngOnInit() {}

  tell(value):void {
    console.log(value)
  }


  delTimer(ch,ind):void{
    console.log(ch,ind);
     this.data.timer[ch].splice(ind,1);
    this.io.socket.emit('TIMER', this.data.timer);
  }

  addTimer(start, end):void {
    console.log(start,end)
    let start_time = start.split(":")
    let end_time = end.split(":");

    let hf = Number(start_time[0]);
    let mf = Number(start_time[1]);
    let ht = Number(end_time[0]);
    let mt = Number(end_time[1]);

    if( isNaN(hf) || isNaN(mf) || isNaN(ht) || isNaN(mt) ){
      alert("Please fill start-end time.");
      return ;
    } 
    let valf = hf*100 + mf;
    let valt = ht*100 + mt;
    
   
    if(valf >= valt){
      alert("Start time muct set before end time.");
      return;
    }


      for(let j of this.data.timer[this.selectedCh]){
        if(valf <= j.hval){
          alert("Timer must be ordered.")
          return;
        }
      }

       console.log(hf,mf,ht,mt, valf, valt);


    var time = {
      'from':{
        'hour': hf,
        'min': mf
      },
      'to':{
        'hour': ht,
        'min': mt
      },
      'timestr': start + "-"+ end,
      'hval': valt
    }
    this.data.timer[this.selectedCh].push(time);
    this.io.socket.emit('TIMER', this.data.timer);
  }


  selectChannel(event):void {
    if(event == 0) return;
    this.selectedCh = event-1;

    for(var i = 0 ; i < this.selected.length ; i++){
      this.selected[i] = false;
    }
    this.selected[event-1] = true;
  }


  changeLstener(value):void {
    console.log(value);
  }

}



