import { Component, OnInit } from '@angular/core';
import {GrobotDataService} from '../../_shared/grobot-data.service'
import {SocketService} from '../../_shared/socket.service';
@Component({
  selector: 'grobot-detecting',
  templateUrl: './detecting.component.html',
  styleUrls: ['./detecting.component.css']
})
export class DetectingComponent implements OnInit {
  showList:any = [true,false,false,false,false]
  selectedCh:number = 0;
  constructor(private data:GrobotDataService,private io:SocketService) { }

  ngOnInit() {
  }
  selectChannel(value):void{
    console.log(value);
    if(value == 0 ) return;
    this.selectedCh = value -1;

    this.showList = [false,false,false,false,false]
    this.showList[value-1] = true;
    
  }


  onVpdFinish(value):void {
    this.data.detecting[this.selectedCh].vpd.set = value;
    this.io.socket.emit('DETECTING', this.data.detecting);
  }

  onSoilFinish(value):void {
    this.data.detecting[this.selectedCh].soil.set = value;
    this.io.socket.emit('DETECTING', this.data.detecting);
  } 

  onTempFinish(value):void {
    this.data.detecting[this.selectedCh].temp.set = value;
    this.io.socket.emit('DETECTING', this.data.detecting);
  }

  submit():void {
    this.io.socket.emit('DETECTING', this.data.detecting);
    alert("saved")
  }
}



