import { Component, OnInit } from '@angular/core';

import {GrobotDataService} from '../_shared/grobot-data.service'
import {SocketService} from '../_shared/socket.service'

@Component({
  selector: 'app-control',
  templateUrl: './control.component.html',
  styleUrls: ['./control.component.css']
})
export class ControlComponent implements OnInit {

  constructor(private data:GrobotDataService,private io:SocketService) { }

  ngOnInit() {}
  setAuto($event):void{
    var mode = {
      'mode': ($event)?"AUTO":"MANUAL"
    }

    this.io.socket.emit("MODE", mode);
    this.data.auto = $event;


    if(!this.data.auto){
      this.data.useDetecting = false;
      this.data.useTimer  = false;
      this.data.useSetpoint = false;
    }
  }

 

  pickAuto(value, index,automode):void{

    if(index == 0){
      this.data.useTimer = value;
      if(value){
        this.data.useDetecting = false;
        this.data.useSetpoint = false;
      }
    }
    else if(index == 1){
      this.data.useDetecting = value;
      if(value){
        this.data.useTimer = false;
        this.data.useSetpoint = false;
      }
      
    }
    else if(index == 2){
      this.data.useSetpoint = value;
      if(value){
        this.data.useDetecting = false;
        this.data.useTimer = false;
      }
    }
    var setmode = {
      'setmode': automode
    }
     this.io.socket.emit('SETMODE', setmode)
  }


  setManual(ch):void {
    console.log(ch);
    this.data.manual[ch] = !this.data.manual[ch]

    var data = {
      'channel': (ch+1),
      'state': this.data.manual[ch]
    }
    this.io.socket.emit('RELAY', data);
  }

}



