import { Component, OnInit } from '@angular/core';
import {GrobotDataService} from '../../_shared/grobot-data.service'
import {SocketService} from '../../_shared/socket.service';



@Component({
  selector: 'grobot-setpoint',
  templateUrl: './setpoint.component.html',
  styleUrls: ['./setpoint.component.css']
})
export class SetpointComponent implements OnInit {
  selectedCh:number = 0;
  showList:any = [true,false,false,false,false]
  constructor(private data:GrobotDataService, private io:SocketService) { }

  ngOnInit() {
  }

  onVpdFinish(from,to):void {
    this.data.setpoint[this.selectedCh].vpd[0] = from;
    this.data.setpoint[this.selectedCh].vpd[1] = to;
    this.io.socket.emit("SET_POINT" , this.data.setpoint);
  }

  onSoilFinish(from,to):void {
    console.log(from,to);
    this.data.setpoint[this.selectedCh].soil[0] = from;
    this.data.setpoint[this.selectedCh].soil[1] = to;
    this.io.socket.emit("SET_POINT" , this.data.setpoint);
  }

  onTempFinish(from,to):void {
    console.log(from,to);
    this.data.setpoint[this.selectedCh].temp[0] = from;
    this.data.setpoint[this.selectedCh].temp[1] = to;
    this.io.socket.emit("SET_POINT" , this.data.setpoint);
  }

  selectChannel(value):void{
    console.log(value);
    if(value == 0 ) return;

    this.selectedCh = value - 1;
    this.showList = [false,false,false,false,false]
    this.showList[value-1] = true;

  }
}



